<template>
  <div>
    <div>身份：
      <select v-model="status">
        <option value="1">外卖小哥</option>
        <option value="0">用户</option>
      </select>
    </div>
    <div>
      账号：<input type="text" v-model="user.name">
    </div>
    <div>
      密码：<input type="text" v-model="user.pass">
    </div>
    <button @click="login()">登录</button>
  </div>
</template>
<script>
  import {login} from "../util/request"
  export default {
    data() {
      return {
        user: {
          name: '',
          pass: ''
        },
        status: 1
      }
    },
    methods: {
      login() {
        login(this.user).then(res => {
          if (res.data.isok) {
            // 当登录成功的时候添加一个成功的标识
            console.log(res);
            sessionStorage.setItem("isLogin", 1)

            // 当登录成功的时候添加一个身份的标识
            sessionStorage.setItem("status", this.status)

            this.$router.push("/index")
          }
        })






        // // 假设账号必须为admin,密码必须为123的时候才能跳转到大首页
        // if (this.user.name == 'admin' && this.user.pass == "123") {
        //   // 当登录成功的时候添加一个成功的标识
        //   sessionStorage.setItem("isLogin",1)

        //   // 当登录成功的时候添加一个身份的标识
        //   sessionStorage.setItem("status",this.status)

        //   this.$router.push("/index")
        // } else {
        //   alert('请检查账号密码')
        // }
      }
    },
    watch: {},
    mounted() {
      console.log('login进来了');
    },
    beforeDestroy() {
      console.log('login销毁了');
    }
  }

</script>
<style>
</style>



